              href="javascript:;"
              class="link d-flex justify-content-center align-items-center"
            >
              <span class="">
                <i
                  class="fa fa-caret-left fa-2x text-muted mr-2"
                  aria-hidden="true"
                ></i>
              </span>
              <span class="mr-2">{{ $t("FORWARD") }}</span>
            </b-link>
            &nbsp;|
            <b-link
              @click.prevent="nextYr()"
              href="javascript:;"
              class="link ml-2 d-flex justify-content-center align-items-center"
            >
              <span>{{ $t("NEXT") }}</span>
              <span class="">
                <i
                  class="fa fa-caret-right fa-2x text-muted ml-2"
                  aria-hidden="true"
                ></i>
              </span>
            </b-link>
          </div>
        </b-col>
        <b-col
          class="d-flex d-none d-md-flex d-lg-none justify-content-end float-right mblAlign"
          cols="12"
          xl="3"
          md="12"
          sm="12"
        >
          <div
            class="py-2 px-2 pillButtons bg-white d-flex justify-content-center align-items-center"
          >
            <b-link
              @click="prvYr()"
              href="javascript:;"
              class="link d-flex justify-content-center align-items-center"
            >
              <span class="">
                <i
                  class="fa fa-caret-left fa-2x text-muted mr-2"
                  aria-hidden="true"
                ></i>
              </span>
              <span class="mr-2">{{ $t("FORWARD") }}</span>
            </b-link>
            &nbsp;|
            <b-link
              @click.prevent="nextYr()"
              href="javascript:;"
              class="link ml-2 d-flex justify-content-center align-items-center"
            >
              <span>{{ $t("NEXT") }}</span>
