<script>
import BarGraph from "@/components/graphs/BarGraph.vue";
// import SubMenuTree from "@/components/SubMenuTree.vue";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import api from "@/shared/api";
import { mapGetters } from "vuex";
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "YearlyExpenseDetails",
  components: { BarGraph },
  props: {},
  data() {
    return {
      selected: null,
      loading: false,
      treeAllData: [],
      selected_expense: {},
      expenseOptions: [],
      copyYearlyData: [],
      selectExpenseItem: this.$t("EXPENSEITEMSELECTION"),
      minVal: 0,
      maxVal: 12,
      yearlyData: [],
      componentKey: 0,
      selectedExpenseItem: "",
    };
  },
  computed: {
    ...mapGetters({
      selected_expense_item: "getSelectedExpenseItem",
      expenseItemCode: "getExpenseItemCode",
      userAccountData: "getUserDetails",
    }),
  },
  mounted() {
    this.getYearlyData();
    this.selectedExpenseItem = this.selected_expense_item.expense_item_nm;
  },
  created() {
    this.getTreeData();
  },
  methods: {
    monthView() {
      this.$router.push({ name: "monthly_expense_details" });
    },
    updateExpenseItem(item) {
      this.selectedExpenseItem = item;
      let treeItem = this._.find(this.treeAllData, { expense_item_nm: item });
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: treeItem.accounts_name_cd,
        expense_item_cd: treeItem.expense_item_cd,
        expense_item_nm: treeItem.expense_item_nm,
      });
      this.getYearlyData();
    },
    drawGraph() {
      setTimeout(() => {
        let treeItem = this._.find(this.treeAllData, {
          expense_item_cd: this.selected_expense.expense_item_cd,
        });
        if (treeItem) {
          this.$store.dispatch("updateSelectedExpenseItem", {
            accounts_name_cd: treeItem.accounts_name_cd,
            expense_item_cd: treeItem.expense_item_cd,
            expense_item_nm: treeItem.expense_item_nm,
          });
        }

        this.getYearlyData();
      }, 0);
    },
    displayButtons() {
      this.treeAllData.forEach((item) => {
        let iterateExpenseItem = {
          text: item.expense_item_nm ? item.expense_item_nm : "",
          value: {
            expense_item_cd: item.expense_item_cd ? item.expense_item_cd : "",
            accounts_name_cd: item.accounts_name_cd
              ? item.accounts_name_cd
              : "",
          },
        };
        if (item.expense_item_nm) {
          this.expenseOptions.push(iterateExpenseItem);
        }
        if (this.selected_expense_item.expense_item_nm) {
          this.selected_expense = {
            accounts_name_cd: this.selected_expense_item.accounts_name_cd,
            expense_item_cd: this.selected_expense_item.expense_item_cd,
          };
        }
      });
    },
    async getYearlyData() {
      this.loading = true;
      let { data } = await api.post(endPoint.EVALUATIONREADYEARLYCOSTINFO, {
        client_no: this.userAccountData.client_account_no,
        accounts_name_cd: this.selected_expense_item.accounts_name_cd,
        expense_item_cd: this.selected_expense_item.expense_item_cd,
      });
      this.selectedExpenseItem = this.selected_expense_item.expense_item_nm;
      if (data.success) {
        this.yearlyData = data.data.yearlyCostInfoAverageObject.map((data) => {
          return { xKey: parseInt(data.year), value: parseFloat(data.cost) };
        });
        this.copyYearlyData = this.yearlyData;
        this.yearlyData = this.yearlyData.slice(0, 12);
        this.yearlyData = this.yearlyData.reverse();
        this.loading = false;
        this.componentKey = this.componentKey + 1;
      }
      if (!data.success) {
        this.loading = false;
      }
    },
    prvYr() {
      let lYrDta = this.copyYearlyData.length;
      let extraLength = lYrDta > this.maxVal ? lYrDta - this.maxVal : 0;
      let count = 0;
      if (count >= extraLength) {
        return;
      }

      if (count < extraLength) {
        this.minVal = this.minVal + 1;
        this.maxVal = this.maxVal + 1;
        count = count + 1;
      }
      this.yearlyData = this.copyYearlyData.slice(this.minVal, this.maxVal);
      this.yearlyData = this.yearlyData.reverse();
      this.componentKey = this.componentKey + 1;
    },
    nextYr() {
      if (this.minVal == 0) {
        return;
      }
      if (this.minVal > 0) {
        this.minVal = this.minVal - 1;
        this.maxVal = this.maxVal - 1;
      }
      this.yearlyData = this.copyYearlyData.slice(this.minVal, this.maxVal);
      this.yearlyData = this.yearlyData.reverse();

      this.componentKey = this.componentKey + 1;
    },
    returnToExpenseEvaluationList() {
      this.$router.push({ name: "expense_evaluation" });
    },
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONCOSTINFOTREELIST,
        {
          client_no: this.userAccountData.client_account_no,
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.treeAllData = responseData;
        let mapData = [];
        let groupedData = this._.groupBy(responseData, "accounts_name_nm");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          groupedData[key].forEach((item) => {
            let bindChildData = {
              text: item.expense_item_nm,
            };
            childrenArray.push(bindChildData);
          });
          let bindTreeData = {
            text: key,
            state: { expanded: true },
            children: childrenArray,
          };
          mapData.push(bindTreeData);
        });
        this.$store.dispatch("updateTree", mapData);
        this.displayButtons();
      }
    },
  },
};
</script>

